import React, { Component } from 'react';
import Top from './NavComponent';
import Footer from './FooterComponent';
import Header from './HeaderComponent';
import {Breadcrumb,BreadcrumbItem,Row} from 'reactstrap';
import {Link} from 'react-router-dom';
import Gallery from 'react-photo-gallery';


class Photos extends Component{
  render(){
    const PHOTO_SET = [
      {
        src: 'assets/images/Imprints gallery/img1.png',
        width: 4,
        height: 3
      },
      {
        src: 'assets/images/Imprints gallery/img2.jpg',
        width: 1,
        height: 1
      },
      {
        src: 'assets/images/Imprints gallery/img3.jpg',
        width: 2,
        height: 3
      },
      {
        src: 'assets/images/Imprints gallery/img4.jpg',
        width: 4,
        height: 5
      },
      {
        src: 'assets/images/Imprints gallery/img5.jpg',
        width: 4,
        height: 2
      },
      {
        src: 'assets/images/Imprints gallery/img6.jpg',
        width: 2,
        height: 3
      },
      {
        src: 'assets/images/Imprints gallery/img7.jpg',
        width: 3,
        height: 3
      },
      {
        src: 'assets/images/Imprints gallery/img8.jpg',
        width: 1,
        height: 1
      },
      {
        src: 'assets/images/Imprints gallery/img9.jpg',
        width: 5,
        height: 3
      },
      {
        src: 'assets/images/Imprints gallery/img10.jpg',
        width: 7,
        height: 3
      },
      {
        src: 'assets/images/Imprints gallery/img11.jpg',
        width: 4,
        height: 3
      },
      {
        src: 'assets/images/Imprints gallery/img12.jpg',
        width: 2,
        height: 3
      },
      {
        src: 'assets/images/Imprints gallery/img13.png',
        width: 4,
        height: 5
      },
      {
        src: 'assets/images/Imprints gallery/img14.png',
        width: 4,
        height: 3
      }
    ];
      return(
        <React.Fragment>
          <Top />
          <Header />
          <div className="container">
            <Row>
              <Breadcrumb>
                  <BreadcrumbItem><Link to="/main">Home</Link></BreadcrumbItem>
                  <BreadcrumbItem active>Gallery</BreadcrumbItem>
              </Breadcrumb>
            </Row>
            <Row>
              <h2>GALLERY</h2>
              <hr />
            </Row>
            <Row>
              <Gallery photos={PHOTO_SET} />
            </Row>
          </div>
          <Footer />
        </React.Fragment>
      );    
  }

}

export default Photos;